.header {
  width: calc(100%);
  height: 60px;
  padding: 6px 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--G5);
  color: #ffffff;
  border-bottom: 1px solid var(--G2);
  position: fixed;
  z-index: 999999;
  box-sizing: border-box;
}

.title {
  font-size: 20px;
  font-weight: 600;
}

.dummy {
  width: 114px;
  height: 32px;
}
